import React, { useState, useEffect } from "react"
import { Bar, HorizontalBar } from "react-chartjs-2"

const Chart = ({ type, data, options }) => {
  let windowWidth = useWindowWidth()
  let width, height

  if (windowWidth >= 1171) {
    width = 60
    height = 15
  } else if (windowWidth < 1771 && windowWidth >= 600) {
    width = 100
    height = 50
  } else {
    width = 100
    height = 120
  }

  return (
    <div className="canvas-container">
      {createChartByType(type, data, width, height, options)}
    </div>
  )
}

const createChartByType = (type, data, width, height, options) => {
  switch (type) {
    case "Bar":
      return <Bar data={data} width={width} height={height} options={options} />
    case "HorizontalBar":
      return (
        <HorizontalBar
          data={data}
          width={width}
          height={height}
          options={options}
        />
      )
    default:
      return null
  }
}

let useWindowWidth = () => {
  const isBrowser = typeof window !== "undefined"
  const [width, setWidth] = useState(isBrowser ? window.innerWidth : 0)

  useEffect(() => {
    if (!isBrowser) return false

    const handleResize = () => setWidth(window.innerWidth)
    window.addEventListener("resize", handleResize)

    return () => {
      window.removeEventListener("resize", handleResize)
    }
  })

  return width
}

export default Chart
